.formulario {

    fieldset {
        border-radius: 1rem;
        box-shadow: 5px 5px 5px;
    }
    p {
        font-size: 1.4rem;
        color: $darkGrey;
        margin: 2rem 0 0 0;
    }
    legend {
        font-size: 2rem;
        color: $darkGrey;
    }
    label {
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }
    input[type="submit"] {
        display: flex;
        justify-content: flex-end;
    }
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $white;
        border: 1px solid $grey;
        border-radius: 1rem;
        margin-bottom: 0;
    }

    .error_message{
        display: none;
    }

    .error {
        .error_message{
            font-size: 1.2rem;
            font-weight: $regular;
            color: $red;
            display: block;
        }
        select,
        input {
            border-color: $red;
            border-width: 2px;
        }
        td { text-align: center;}
        
        p {
            text-align: right;
            font-size: 1.2rem;
        }
    }

    @include tablet {
        .one-line { 
            display: grid;
            grid-template-columns: repeat(2, 1fr); 
        }

        .two-thirds { 
            display: grid;
            grid-template-columns: 2fr 1fr; 
        }
    
        .two-one-one { 
            display: grid;
            grid-template-columns: 2fr 1fr 1fr; 
        }    

        .tres-columnas { 
            display: grid;
            grid-template-columns: repeat(3, 1fr); 
        }
        
        .field {
            display: flex;
            flex-direction: column;
        }
        .checkbox {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-around;
            input[type="checkbox"]{ width: auto;}

        }

    }

    .botonera {
        display: flex;
        justify-content: space-between;
    }

    .overide {
        background-color: rgba(0,0,0,.9);
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        visibility: hidden;
    }

    .overide.active {
        visibility: visible;
    }
    
    .popup_form {
        background-color: #f8f8f8;
        border-radius: 1rem;
        padding: 2rem;
        text-align: center;
        width: 60rem;
        transition: 1s ease all;
        transform: scale(.7);
        opacity: 0;
        .yes_no_buttons {
            display: flex;
            justify-content: space-around;
        }
    }
    
    .popup_form.active {
        animation: entrada .8s ease .5s forwards;
    }
    
    @keyframes entrada {
        from {
            transform: scale(.7);
            opacity: 0;
        }
        to {
            transform: scale(1);
            opacity: 1;
        }
    }

}