html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*, *:before, *:after {box-sizing: inherit}

body {
    font-family: $main_font;
    font-size: 1.6rem;
    line-height: 1.8;
}

p {
    color: $black;
    font-size: 2rem;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
    padding-bottom: 10rem;
}

a {text-decoration: none}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    font-family: $main_font;
    margin: calc($separation / 2) 0;
    font-weight: 300;
    text-align: center;
    
}

h1 {
    font-size: 3.8rem;
    font-weight: $bold;
}
h2 {font-size: 3.4rem}
h3 {font-size: 3rem}
h4 {font-size: 2.6rem}