.footer {
    padding: 1rem 0 3rem 0;
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 100px;


    
    a {
        width: 20%;
        position: absolute;
        right: 0;
        bottom: 0;
    }
    .copyright {
        width: 80%;
        color: $black;
        text-align: center;
    }
}

.content-footer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}