html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  padding-bottom: 10rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
  font-weight: 700;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.color-red {
  color: rgb(255, 0, 0);
}

.boton-rojo {
  background-color: rgb(189, 7, 7);
  color: #000;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 1rem;
}
.boton-rojo:hover {
  background-color: #a40606;
  cursor: pointer;
}

.botones-acceso {
  background-color: #4e79ca;
  color: #fff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  border-radius: 1rem;
}
.botones-acceso:hover {
  background-color: #3b6ac4;
  cursor: pointer;
}

.button-yes-no {
  padding: 0 2rem;
  height: 4rem;
  line-height: 4rem;
  color: #fff;
  background-color: #5E7DE3;
  border-radius: 0.3rem;
  cursor: pointer;
  transition: 0.3s ease all;
}
.button-yes-no:hover {
  background-color: #244cd0;
}

.no-webp .body {
  background-image: url(../img/Fondo.jpg);
}

.webp .body {
  background-image: url(../img/Fondo.webp);
}

.body {
  background-size: cover;
  background-position: center top;
}

.overlay {
  background: -moz-linear-gradient(top, rgba(238, 238, 238, 0.35) 0%, rgb(238, 238, 238) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(238, 238, 238, 0.35) 0%, rgb(238, 238, 238) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(238, 238, 238, 0.35) 0%, rgb(238, 238, 238) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#59eeeeee", endColorstr="#eeeeee",GradientType=0 );
  /* IE6-9 */
  position: absolute;
  margin: 0;
  width: 100%;
  height: 100%;
}

.popup {
  position: fixed;
  top: 0;
  right: 0;
  text-align: right;
  width: 60rem;
  opacity: 0;
  transition: 0.3s ease all;
  transform: scale(0.7);
}

.popup div {
  opacity: 0;
}

.popup.active {
  opacity: 1;
  transform: scale(1);
}

.popup.active div {
  animation: entradaTitulo 0.8s ease 0.5s forwards;
}

@keyframes entradaTitulo {
  from {
    transform: translateY(-25px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.footer {
  padding: 1rem 0 3rem 0;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100px;
}
.footer a {
  width: 20%;
  position: absolute;
  right: 0;
  bottom: 0;
}
.footer .copyright {
  width: 80%;
  color: #000000;
  text-align: center;
}

.content-footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.formulario fieldset {
  border-radius: 1rem;
  box-shadow: 5px 5px 5px;
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input[type=submit] {
  display: flex;
  justify-content: flex-end;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 0;
}
.formulario .error_message {
  display: none;
}
.formulario .error .error_message {
  font-size: 1.2rem;
  font-weight: 400;
  color: rgb(189, 7, 7);
  display: block;
}
.formulario .error select,
.formulario .error input {
  border-color: rgb(189, 7, 7);
  border-width: 2px;
}
.formulario .error td {
  text-align: center;
}
.formulario .error p {
  text-align: right;
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .formulario .one-line {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .formulario .two-thirds {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
  .formulario .two-one-one {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
  }
  .formulario .tres-columnas {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .formulario .field {
    display: flex;
    flex-direction: column;
  }
  .formulario .checkbox {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
  .formulario .checkbox input[type=checkbox] {
    width: auto;
  }
}
.formulario .botonera {
  display: flex;
  justify-content: space-between;
}
.formulario .overide {
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
}
.formulario .overide.active {
  visibility: visible;
}
.formulario .popup_form {
  background-color: #f8f8f8;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  width: 60rem;
  transition: 1s ease all;
  transform: scale(0.7);
  opacity: 0;
}
.formulario .popup_form .yes_no_buttons {
  display: flex;
  justify-content: space-around;
}
.formulario .popup_form.active {
  animation: entrada 0.8s ease 0.5s forwards;
}
@keyframes entrada {
  from {
    transform: scale(0.7);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-top: 1rem;
  align-items: center;
}
.header p {
  text-align: right;
  font-size: 1.5rem;
  margin: 0;
  padding: 0;
  line-height: 1.8rem;
  font-weight: 700;
}
.header .banderas img {
  width: 3%;
}
.header .banderas .disable {
  filter: blur(1px);
}
.header .desplegable {
  background-color: red;
  position: absolute;
  right: 1rem;
  top: 4rem;
}
.header .desplegable a {
  padding: 1rem;
}

table.tables {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0 1rem;
  border-radius: 1rem;
  box-shadow: 5px 5px 5px;
  border: 1px solid black;
}
table.tables .columna-botones {
  width: 12rem;
}
table.tables thead th {
  border-bottom: 1px solid black;
}
table.tables .boton-update {
  background-color: red;
  color: black;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0;
  text-align: center;
  margin-top: 0;
  display: block;
  border: none;
  border-radius: 1rem;
}
table.tables .boton-update:hover {
  background-color: #e60000;
  cursor: pointer;
}

table.resumen {
  margin-bottom: 1rem;
  width: 100%;
  border-radius: 1rem;
  box-shadow: 5px 5px 5px;
  border: 1px solid black;
}
table.resumen .columna-botones {
  width: 2rem;
}
table.resumen thead th {
  border-bottom: 1px solid black;
}
table.resumen tfoot {
  font-weight: 700;
}

.actions {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.actions a {
  color: #000000;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}