.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 1rem;
    margin-right: 1rem;
    margin-top: 1rem;
    align-items: center;
    p{ 
        text-align: right;
        font-size: 1.5rem;
        margin: 0;
        padding: 0;
        line-height: 1.8rem;
        font-weight: $bold;
    }
    
    .banderas {
        img {
            width: 3%;
        }
        .disable {
            filter: blur(1px);
        }
    }
    .desplegable {
        background-color: red;
        position: absolute;
        right: 1rem;
        top: 4rem;
        a {
            padding: 1rem;
        }
    }
}