.boton-rojo {@include boton($red, #000, inline-block)}
.botones-acceso {@include boton(#4e79caff, #fff, inline-block)}

.button-yes-no {
    padding: 0 2rem;
    height: 4rem;
    line-height: 4rem;
    color: #fff;
    background-color: #5E7DE3;
    border-radius: .3rem;
    cursor: pointer;
    transition: .3s ease all ;
    &:hover {
        background-color: darken($color: #5E7DE3, $amount: 15);
    }
}