/** Media Queries **/
@mixin phone {
    @media (min-width: #{$telefono}) {@content;}
}

@mixin tablet {
    @media (min-width: #{$tablet}) {@content;}
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {@content;}
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgColor, $color, $display) {
    background-color: $bgColor;
    color: $color;
    font-weight: $bold;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: calc($separation / 2);
    display: $display;
    border: none;
    border-radius: 1rem;
    &:hover {
        background-color: darken($color: $bgColor, $amount: 5);
        cursor: pointer;
    }
}