table.tables {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0 1rem;
    border-radius: 1rem;
    box-shadow: 5px 5px 5px;
    border: 1px solid black;
    
    .columna-botones {width: 12rem; }

    thead {
        th { border-bottom: 1px solid black; }
    }

    .boton-update {
        background-color: red;
        color: black;
        font-weight: $bold;
        text-decoration: none;
        text-transform: uppercase;
        padding: 0;
        text-align: center;
        margin-top: 0;
        display: block;
        border: none;
        border-radius: 1rem;
        &:hover {
            background-color: darken($color: red, $amount: 5);
            cursor: pointer;
        }
    }
}

table.resumen {
    margin-bottom: 1rem;
    width: 100%;
    border-radius: 1rem;
    box-shadow: 5px 5px 5px;
    border: 1px solid black;
    
    .columna-botones {width: 2rem; }

    thead {
        th { border-bottom: 1px solid black; }
    }
    tfoot { font-weight: $bold;}
}