.no-webp .body {background-image: url(../img/Fondo.jpg)}
.webp .body {background-image: url(../img/Fondo.webp)}

.body {
    background-size: cover;
    background-position: center top;
}

.overlay {
    background: -moz-linear-gradient(top,  rgba(238,238,238,0.35) 0%, rgba(238,238,238,1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top,  rgba(238,238,238,0.35) 0%,rgba(238,238,238,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  rgba(238,238,238,0.35) 0%,rgba(238,238,238,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#59eeeeee', endColorstr='#eeeeee',GradientType=0 ); /* IE6-9 */
    position: absolute;
    margin: 0;
    width: 100%;
    height: 100%;
}

.popup {
    position: fixed;
    top: 0;
    right: 0;
    text-align: right;
    width: 60rem;
    opacity: 0;
    transition: .3s ease all;
    transform: scale(0.7);
}

.popup div { opacity: 0; }

.popup.active {
    opacity: 1;
    transform: scale(1);
}

.popup.active div {
    animation: entradaTitulo .8s ease .5s forwards;
}

@keyframes entradaTitulo {
    from {
        transform: translateY(-25px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}
